

import React, { useState } from 'react';

import AnchorStyled from './AnchorStyled';
import Breaker from './Breaker';
import DividerStyled from './DividerStyled';
import IndentorStyled from './IndentorStyled';
import ParagraphStyled from './ParagraphStyled';

import Header from './Header';
import Footer from './Footer';

import { Link } from 'react-router-dom';

const HelloWorld = ( { title } ) => 
{
    // for showing and hiding lesson review
    const [ showReview, setShowReview ] = useState( false );
    const toggleReview = ( ) => (
        setShowReview( showReview => !showReview )
    )

    //  convenience variables -> also possible to use React hooks to change the value, but doing so may
    //  create too many function calls, since each increment and reset would each require a call
    let lineNumber = 1, lineNumber2 = 1;
    
    //  to better work with React, since JSX (not JS) always interpolates the value in template string,
    //  and string escape sequence does not seem to work
    let comment = "//";

    return (
        <>
            <DividerStyled>

                {/* page title */}
                <Header main>
                    { 
                        title && 
                        (<>{ title }</>)
                    }
                </Header>
                {/* page title */}

                {/* lecture content */}
                <ParagraphStyled>
                    Traditionally, the first program taught in any programming language is the program that prints the 
                    Hello World message. The successful completion of which requires the proper installation of any 
                    software needed to compile the source code, the actual complication and linkage, program execution, 
                    and locating where the program output is generated. The installation step has already been completed 
                    during the previous lesson; in this lesson, the developer will learn to run the JS file with the 
                    Node.js runtime.
                </ParagraphStyled> 

            </DividerStyled>

            <DividerStyled>

                <Header subsection>
                    1. Using the Runtime to Execute JS
                </Header>

                <ParagraphStyled>
                    Comments in JS are created with double forward slashes (//), and since comments do not affect program 
                    execution, they are not shown in the output. Create a JS file named helloworld.js with the following 
                    content, and then issue this command in the command line interpreter: node helloworld.js to execute the 
                    program. Verify the output.
                </ParagraphStyled>

            </DividerStyled>

            {/* helloworld.js */}
            <DividerStyled programCode>
            
                <Header fileName>
                    helloworld.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber++}<IndentorStyled />{comment} This is a comment.<Breaker />
                    Line {lineNumber++}<IndentorStyled />{comment} console.log() is a print function available to JS programs.<Breaker />
                    Line {lineNumber++}<IndentorStyled />console.log("Hello World.");
                </ParagraphStyled>

            </DividerStyled>
            {/* helloworld.js */}

            {/* helloworld.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    Hello World.
                </ParagraphStyled>

            </DividerStyled>
            {/* helloworld.js output */}

            <DividerStyled>

                {/* spacer from the last program output to make the reading easier */}
                <Breaker />

                <Header subsection>
                    2. Explanation of the Program
                </Header>

                <ParagraphStyled>
                    As explained in the program comment, console.log() is a print function available to JS programs: it is 
                    not part of the JS programming language. Hello World is the argument we pass to the print function to 
                    print to the console, which is shown in the command line interpreter. Although the Node.js runtime is 
                    being used, no additional configuration is required should the developer choose to switch to the browser 
                    environment.
                </ParagraphStyled>

                <ParagraphStyled>
                    If the console is not already visible in the web browser, the developer can view the console by 
                    choosing the corresponding menu command, which varies from one browser to another. Keyboard shortcuts 
                    are available for select browsers on select operating systems. For example, the F12 function key can be 
                    used to open the console in Google Chrome and Mozilla Firefox in Windows. Consult the documentation of 
                    your browser if necessary. Unlike programming languages such as C++ and Java, the Hello World program 
                    need not be written inside a main function, nor do any I/O libraries need to be explicitly included.
                </ParagraphStyled>

            </DividerStyled>

            <DividerStyled>
                {/* show review */}
                { !showReview &&
                    <AnchorStyled
                        recap
                        onClick={ toggleReview }>
                        Let's Recap
                    </AnchorStyled>
                }
                {/* show review */}

                {/* lesson review */}
                {
                    showReview && (
                        <>
                        <ParagraphStyled review>
                            <Header main>
                                Recap
                            </Header>
                            ({lineNumber2++}) JS supports C-style comments, and therefore comments can be preceded by {comment}.<Breaker />
                            ({lineNumber2++}) The console.log() print function is available to JS programs.<Breaker />
                            ({lineNumber2++}) For the web browser, console.log() prints in the JS Developer Console.<Breaker />
                            ({lineNumber2++}) The F12 function key can be used to make the Console visible on select browsers.
                        </ParagraphStyled>
                        </>
                    )
                }
                {/* lesson review */}

                {/* hide review */}
                {
                    showReview && (
                        <AnchorStyled 
                                recapDone
                                onClick={ toggleReview }>
                                I am done reviewing!
                        </AnchorStyled>
                    )
                }
                {/* hide review */}

            </DividerStyled>
            
            <DividerStyled pageLinker>
                <Footer>
                    <Link to='/setup'>&lArr; Back</Link>
                </Footer>

                <Footer>
                    <Link to='/data-types-and-structures'>Forward &rArr;</Link>
                </Footer>
            </DividerStyled>
        </>
    )
}
export default HelloWorld;