

import React, { useState } from 'react';
import AnchorStyled from './AnchorStyled';
import Breaker from './Breaker';
import DividerStyled from './DividerStyled';
import ParagraphStyled from './ParagraphStyled';
import Header from './Header';
import Footer from './Footer';

import { ReactComponent as HomeBuilding } from '../images/homeBuilding.svg';

import { Link } from 'react-router-dom';

const Home = ( { title } ) => 
{
    // for showing and hiding lesson review
    const [ showReview, setShowReview ] = useState( false );
    const toggleReview = ( ) => (
        setShowReview( showReview => !showReview )
    )

    //  convenience variable -> also possible to use React hooks to change the value, but doing so may
    //  create too many function calls, since each increment and reset would each require a call
    let lineNumber = 1;

    return (
        <>
        
            {/* main image */}
            <DividerStyled mainImage>
                <HomeBuilding />
            </DividerStyled>
            {/* main image */}

            {/* main content */}
            <DividerStyled>

                {/* page title */}
                <Header main>
                    { 
                        title && 
                        (<>{ title }</>)
                    }
                </Header>
                {/* page title */}
                
                {/* lecture content */}
                <ParagraphStyled>
                    Welcome to JST  our online JavaScript Tutor. Our goal is to offer an introductory presentation of 
                    the JavaScript (JS) programming language. JS is a high-level programming language that provides 
                    multiparadigm support, including procedural, functional, object-oriented, and event-driven. In 
                    addition to server-side support, JS is also a core technology used in web browsers for 
                    client-side programming that specifies the behavior of web pages.                             
                </ParagraphStyled>

                <ParagraphStyled>
                    The targeted audience of JST are developers who are familiar with basic concepts of procedural and 
                    object-oriented programming, used in languages such as C++ or Java: data structures, control 
                    mechanisms, functions, and data encapsulation. JST has not been developed for novice-level users.
                </ParagraphStyled>               

                <ParagraphStyled>
                    Typical JST users are senior-level computer science students and professionals who seek brief and 
                    introductory exposure to the language for evaluation of its expressive power and area of use. The 
                    volume of materials being presented is appropriate for study over three afternoons, approximately 
                    twelve hours. Familiarity with JST's content will enable users to understand the role and the 
                    structure of JS programs and to decide whether to continue a more detailed study of JS based on 
                    professional literature.
                </ParagraphStyled>

                <ParagraphStyled>
                    Although not a requirement, a cursory knowledge of HTML may be beneficial to understanding the 
                    upcoming lessons. Therefore, a primer on HTML has been prepared. If the student is foreign to the 
                    technology, a brief study using the primer is recommended.
                </ParagraphStyled>
                {/* lecture content */}

                {/* show review */}
                { !showReview &&
                    <AnchorStyled
                        recapHome
                        onClick={ toggleReview }>
                        Let's Recap
                    </AnchorStyled>
                }
                {/* show review */}

                {/* lesson review */}
                {
                    showReview && (
                        <>
                        <ParagraphStyled review>
                            <Header main>
                                Recap
                            </Header>
                            ({lineNumber++}) JS supports multiparadigm support.<Breaker />
                            ({lineNumber++}) JS facilitates user interaction on the client side.<Breaker />
                            ({lineNumber++}) JST assumes familiarity with basic programming concepts.<Breaker />
                            ({lineNumber}) knowledge of HTML may be conducive for learning JS.
                        </ParagraphStyled>
                        </>
                    )
                }
                {/* lesson review */}

                {/* hide review */}
                {
                    showReview && (
                        <AnchorStyled 
                                recapDoneHome
                                onClick={ toggleReview }>
                                I am done reviewing!
                        </AnchorStyled>
                    )
                }
                {/* hide review */}

            </DividerStyled>
            {/* main content */}

            {/* pageLinker */}
            <DividerStyled pageLinker>

                <Footer>
                    <Link to='/html-primer'>&lArr; HTML Primer</Link>
                </Footer>

                <Footer>
                        <Link to='/setup'>Skip HTML Primer &rArr;</Link>
                </Footer>
                
            </DividerStyled>
            {/* pageLinker */}

            {/* main content */}
        </>
    )
}
export default Home;