import React, { useState } from 'react';

import AnchorStyled from './AnchorStyled';
import Bracketer from './Bracketer';
import Breaker from './Breaker';
import DividerStyled from './DividerStyled';
import DoubleIndentorStyled from './DoubleIndentorStyled';
import IndentorStyled from './IndentorStyled';
import ParagraphStyled from './ParagraphStyled';
import TripleIndentorStyled from './TripleIndentorStyled';

import Header from './Header';
import Footer from './Footer';

import { Link } from 'react-router-dom';

const SampleWebApplication = ( { title } ) => 
{
    // for showing and hiding lesson review
    const [ showReview, setShowReview ] = useState( false );
    const toggleReview = ( ) => (
        setShowReview( showReview => !showReview )
    )

    //  convenience variables
    let lineNumber = 1, lineNumber2 = 1, lineNumber3 = 1, lineNumber4 = 1;
    //  to better work with React
    let comment = "//";

    return (
        <>
            <DividerStyled>

                {/* page title */}
                <Header main>
                    { 
                        title && 
                        (<>{ title }</>)
                    }
                </Header>
                {/* page title */}

                {/* lecture content */}
                <ParagraphStyled>
                    Apple Safari, Google Chrome, and Mozilla Firefox are all examples of modern web browsers. Web browsers 
                    can be used to access a web site, which is composed of a number of web pages. While a text document 
                    devoid of any stylistic formatting can often still be displayed in the browser, the use of the 
                    HyperText Markup Language (HTML) is recommended to ensure browser compatibility.
                </ParagraphStyled> 

                <ParagraphStyled>
                    The function of the markup language is similar to the comments in the source code: to elucidate. An 
                    HTML element is normally composed of two parts: the starting tag and the ending tag. The starting tag 
                    has three individual components: the less than sign (<Bracketer>), the tag name, and the greater than sign
                    (</Bracketer>). In the case of the ending tag, an extra component is included: the forward slash (/). For 
                    example, for the HTML title element, <Bracketer>title</Bracketer> is the starting tag, and <Bracketer>
                    /title</Bracketer> is the ending tag. Some HTML elements do not have an ending tag.
                </ParagraphStyled>

                <ParagraphStyled>
                    HTML elements are annotations which add meaning and structure to the page. For example, some elements 
                    add metadata information about the page (the <Bracketer>head</Bracketer> element), some elements delimit 
                    the visible area on the page (the <Bracketer>body</Bracketer> element), some elements attract greater 
                    attention from the viewer (the <Bracketer>h1</Bracketer> through <Bracketer>h6</Bracketer> heading 
                    elements), some elements allow assets to be embedded on the page (the <Bracketer>img</Bracketer> element), 
                    and some elements create hypertext links that connect with other pages (the <Bracketer>link</Bracketer> 
                    element). The code below demonstrates select HTML elements in an HTML file named index.html. To render 
                    the page, simply navigate to the directory in which the HTML file resides, and open the file in the web 
                    browser of your choice. The output will differ, depending on your browser and the random image that has 
                    been generated.
                </ParagraphStyled>

            </DividerStyled>

            {/* index.html */}
            <DividerStyled programCode>
                <Header fileName>
                    index.html
                </Header>

                <ParagraphStyled>
                    Line {lineNumber++}<IndentorStyled /><Bracketer>!-- This is a comment. --</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>!DOCTYPE html</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>html</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>!-- The head contains meta information about the page. --</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>head</Bracketer><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>title</Bracketer>Page Title<Bracketer>/title</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>/head</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>!-- The body contains the visible elements on the page. --</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>body</Bracketer><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>!-- The h1 through h6 tags create headers on the page. --</Bracketer><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>h1</Bracketer>A heading<Bracketer>h1</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>button</Bracketer><Breaker />
                    Line {lineNumber++}<TripleIndentorStyled /><Bracketer>!-- The a (anchor) tag creates a hypertext link to other resources. --</Bracketer><Breaker />
                    Line {lineNumber++}<TripleIndentorStyled /><Bracketer>a href="https://cs.sfsu.edu/" target="_blank"</Bracketer>SFSU CS Department<Bracketer>/a</Bracketer><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>/button</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>!-- The img tag inserts an image on the page. --</Bracketer><Breaker />
                    Line {lineNumber++}<DoubleIndentorStyled /><Bracketer>img src="https://placeimg.com/640/480/nature" alt="random image"</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>/body</Bracketer><Breaker />
                    Line {lineNumber++}<IndentorStyled /><Bracketer>/html</Bracketer><Breaker />
                </ParagraphStyled>
            </DividerStyled>
            {/* index.html */}

            {/* index.html output */}
            <DividerStyled programOutput>
                <Header output>
                    output
                </Header>
                <ParagraphStyled>
                    While the exact composition will depend on the browser and the screen resolution, the page will render 
                    a heading, a random image from an online source, and a link to the San Francisco State Computer Science 
                    Department.
                </ParagraphStyled>
            </DividerStyled>
            {/* index.html output */}

            <DividerStyled>
                <ParagraphStyled>
                    In production, the JS files are normally embedded in the HTML files, and therefore in the event of an error, 
                    it can originate from a variety of sources: the HTML file, the linkage, the JS file, or all three. To 
                    minimize the sources of errors, the installation of the Node.js runtime environment will be covered in the 
                    next lesson. With the runtime, the single possible source of error is in the JS file.
                </ParagraphStyled>
            </DividerStyled>
            
            <DividerStyled>
                <Header subsection>1. Embedding the JS File</Header>
                <ParagraphStyled>
                    As previously shown, in order for an image to be displayed on the page, two steps are required: 
                    embedding the source, and browser rendering. Similarly, before JS can be executed on the page, the 
                    source file needs to be embedded. Create a JS file named script.js with the content below. 
                </ParagraphStyled>
            </DividerStyled>

            {/* script.js */}
            <DividerStyled programCode>
                <Header fileName>
                    script.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber2++}<IndentorStyled />{comment} This is a comment.<Breaker />
                    Line {lineNumber2++}<IndentorStyled />{comment} JS can insert HTML elements into the page.<Breaker />
                    Line {lineNumber2++}<IndentorStyled />document.write('<Bracketer>h1</Bracketer>A heading<Bracketer>/h1</Bracketer>');<Breaker />
                    Line {lineNumber2++}<IndentorStyled />document.write('<Bracketer>button</Bracketer><Breaker />
                    <TripleIndentorStyled /><Bracketer>a href="https://cs.sfsu.edu/" target="_blank"</Bracketer>SFSU CS Department<Bracketer>/a</Bracketer><Breaker />
                    <TripleIndentorStyled /><Bracketer>/button</Bracketer>');<Breaker />
                    Line {lineNumber2++}<IndentorStyled />document.write('<Breaker />
                    <TripleIndentorStyled /><Bracketer>img src="https://placeimg.com/640/480/nature" alt="random image"</Bracketer><Breaker />
                    <TripleIndentorStyled /><Bracketer>/img</Bracketer>');<Breaker />
                </ParagraphStyled>
            </DividerStyled>
            {/* script.js */}

            <DividerStyled>
                <ParagraphStyled>
                    Now, create an HTML file named index2.html that links to the JS file, as shown. Open the HTML file in the 
                    browser.
                </ParagraphStyled>
            </DividerStyled>
    
            {/* index.html */}
            <DividerStyled programCode>
                <Header fileName>
                    index.html
                </Header>

                <ParagraphStyled>
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>!DOCTYPE html</Bracketer><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>html</Bracketer><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>head</Bracketer><Breaker />
                    Line {lineNumber3++}<DoubleIndentorStyled /><Bracketer>title</Bracketer>Page Title<Bracketer>/title</Bracketer><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>/head</Bracketer><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>body</Bracketer><Breaker />
                    Line {lineNumber3++}<DoubleIndentorStyled /><Bracketer>!-- The JS file is embedded here. --</Bracketer><Breaker />
                    Line {lineNumber3++}<DoubleIndentorStyled /><Bracketer>script src="script.js"</Bracketer><Bracketer>/script</Bracketer><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>/body</Bracketer><Breaker />
                    Line {lineNumber3++}<IndentorStyled /><Bracketer>/html</Bracketer><Breaker />
                </ParagraphStyled>
            </DividerStyled>
            {/* index.html */}

            {/* index.html output */}
            <DividerStyled programOutput>
                <Header output>
                    output
                </Header>
                <ParagraphStyled>
                    The output should be identical to the output of the previous HTML file, namely index.html, 
                    with the sole exception of the randomly inserted image.
                </ParagraphStyled>
            </DividerStyled>
            {/* index.html output */}

            <DividerStyled>
                <ParagraphStyled>
                    Although the output is nearly the same as those generated from index.html, how the output has been 
                    generated is not the same. In index.html, the output has been generated by the HTML elements. In 
                    index2.html, the output has also been generated by the HTML elements, but these elements have been 
                    inserted: JS has the ability to add, change, and even delete the contents of the HTML file.
                </ParagraphStyled>

                <ParagraphStyled>
                    Note that while it is entirely possible to mix the HTML code with the JS code in the same HTML file, such 
                    practice is not recommended and therefore will not be discussed: the interface is to be separated from 
                    the computation.
                </ParagraphStyled>
            </DividerStyled>
            {/* lecture content */}

            <DividerStyled>
                {/* show review */}
                { !showReview &&
                    <AnchorStyled
                        recap
                        onClick={ toggleReview }>
                        Let's Recap
                    </AnchorStyled>
                }
                {/* show review */}

                {/* lesson review */}
                {
                    showReview && (
                        <>
                        <ParagraphStyled review>
                            <Header main>
                                Recap
                            </Header>
                            ({lineNumber4++}) The use of HTML is recommended to ensure compatibility between web browsers.<Breaker />
                            ({lineNumber4++}) HTML is not a programming language and does not support programming logic.<Breaker />
                            ({lineNumber4++}) Most HTML elements are made up of a starting tag and an ending tag.<Breaker />
                            ({lineNumber4++}) JS can be embedded in HTML using the script tags.<Breaker />
                            ({lineNumber4++}) JS can insert HTML elements into an HTML document.<Breaker />
                        </ParagraphStyled>
                        </>
                    )
                }
                {/* lesson review */}

                {/* hide review */}
                {
                    showReview && (
                        <AnchorStyled 
                                recapDone
                                onClick={ toggleReview }>
                                I am done reviewing!
                        </AnchorStyled>
                    )
                }
                {/* hide review */}

            </DividerStyled>
            
            <DividerStyled pageLinker>
                <Footer>
                    <Link to='/js-programming-techniques'>&lArr; Back</Link>
                </Footer>

                <Footer>
                    <Link to='/road-ahead'>Forward &rArr;</Link>
                </Footer>
            </DividerStyled>
        </>
    )
}
export default SampleWebApplication;