import React, { useState } from 'react';

import AnchorStyled from './AnchorStyled';
import Breaker from './Breaker';
import DividerStyled from './DividerStyled';
import IndentorStyled from './IndentorStyled';
import ParagraphStyled from './ParagraphStyled';
import TripleIndentorStyled from './TripleIndentorStyled';

import Header from './Header';
import Footer from './Footer';

import { Link } from 'react-router-dom';

const DataTypesAndStructures = ( { title } ) => 
{
    // for showing and hiding lesson review
    const [ showReview, setShowReview ] = useState( false );
    const toggleReview = ( ) => (
        setShowReview( showReview => !showReview )
    )

    //  convenience variables -> also possible to use React hooks to change the value, but doing so may
    //  create too many function calls, since each increment and reset would each require a call
    let lineNumber = 1, lineNumber2 = 1, lineNumber3 = 1, lineNumber4 = 1, lineNumber5 = 1, lineNumber6 = 1;
    let lineNumber7 = 1, lineNumber8 = 1, lineNumber9 = 1, lineNumber10 = 1, lineNumber11 = 1;

    //  to better work with React, since JSX (not JS) always interpolates the value in template string,
    //  and string escape sequence does not seem to work
    let comment = "//";
    let firstName = "{firstName}";
    let lastName = "{lastName}";
    let email = "{email}";
    let quotationMarks = "{singleQuotationMarks === doubleQuotationMarks}.";
    let injectedVariable = "{injectedVariable}";
    let age = "{age}";
    let templateString = "{stringConcatenation === templateString }.";
    let isLoggedIn = "{isLoggedIn}";
    let number1a = "{5/10}";
    let number1b = "{10/5}";
    let number1c = "{(10/5).toFixed(1)}";
    let number2a = "{6/5}";
    let number2b = "{-6/5}";
    let number2c = "{Math.floor(6/5)}";
    let number2d = "{Math.ceil(-6/5)}";
    let object1a = "{}";
    let object1b = "{name: 'JST'}";
    let object1c = "{ name: 'JST', gender: 'female' }";

    return (
        <>
            <DividerStyled>

                {/* page title */}
                <Header main>
                    { 
                        title && 
                        (<>{ title }</>)
                    }
                </Header>
                {/* page title */}

                {/* lecture content */}
                <ParagraphStyled>
                    In this lesson, various data types will be presented, including Boolean, Number, Object, and String. 
                    The Number and Boolean data types are especially useful for the next lesson, Expressions and 
                    Operators. This lesson will conclude with a brief discussion on the array data structure.
                </ParagraphStyled> 

            </DividerStyled>

            <DividerStyled>

                <Header subsection>
                    1. String
                </Header>

                <ParagraphStyled>
                    In many programming languages, a string is composed of two characters or more. A character is one symbol 
                    from a given character set, such as ASCII. However, in JS, there is no character data type: there is only 
                    the string type, even if it is an empty string with no content. Unlike typed languages such as C++ and 
                    Java, no type declaration is necessary in JS, and the data type of the variable is inferred by the 
                    assigned value. If no value has been assigned, the variable has the value of undefined. The String type is 
                    appropriate for saving textual data, such as names and email addresses. The following program defines 
                    three string variables with their stored values displayed in the console. Variables in JS are defined with 
                    the let keyword.
                </ParagraphStyled>

            </DividerStyled>

            {/* string1.js */}
            <DividerStyled programCode>

                <Header fileName>
                    string1.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber++}<IndentorStyled />{comment} Declare a variable with the let keyword.<Breaker />
                    Line {lineNumber++}<IndentorStyled />let firstName = 'John';<Breaker />
                    Line {lineNumber++}<IndentorStyled />console.log( `firstName = ${ firstName }.` );<Breaker />
                    Line {lineNumber++}<IndentorStyled />let lastName = "Doe";<Breaker />
                    Line {lineNumber++}<IndentorStyled />console.log( `lastName = ${ lastName }.` );<Breaker />
                    Line {lineNumber++}<IndentorStyled />let email = `JD@JST.EDU`;<Breaker />
                    Line {lineNumber++}<IndentorStyled />console.log( `email = ${ email }.` );<Breaker />
                </ParagraphStyled>
            </DividerStyled>
            {/* string1.js */}

            {/* string1.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    firstName = John.<Breaker />
                    lastName = Doe.<Breaker />
                    email = JD@JST.EDU.<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* string1.js output */}

            <DividerStyled>

                <ParagraphStyled>
                    Examining the code in string1.js, the astute developer may have noticed that there is more than one way to declare a String variable 
                    in JS. However, while there appears to be three different ways (using single quotes, using double quotes, or using backticks), there are 
                    effectively only two ways: with quotation marks (single or double) or with backticks (`). Since JS does not distinguish between a 
                    character and a string, strings delimited by single quotes and double quotes are completely interchangeable. The following program 
                    verifies their equivalence.
                </ParagraphStyled>

            </DividerStyled>
            
            {/* string2.js */}
            <DividerStyled programCode>

                <Header fileName>
                    string2.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber2++}<IndentorStyled />let singleQuotationMarks = 'hello';<Breaker />
                    Line {lineNumber2++}<IndentorStyled />let doubleQuotationMarks = "hello";<Breaker />
                    Line {lineNumber2++}<IndentorStyled />{comment} Use the equality operator to test whether they are equal.<Breaker />
                    Line {lineNumber2++}<IndentorStyled />console.log( `singleQuotationMarks === doubleQuotationMarks:<Breaker />
                    <TripleIndentorStyled />${quotationMarks}` );<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* string2.js */}

            {/* string2.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    singleQuotationMarks === doubleQuotationMarks: true.<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* string2.js output */}

            <DividerStyled>
                <ParagraphStyled>
                    The second type of strings are defined by backticks (`), often known as the template string. This type of strings allows values to be 
                    interpolated inside the string, similar to the f-string that is available in the Python programming language. That is, instead of using 
                    string concatenation with the plus operator (+), template strings permit variables of other types to be injected directly inside the 
                    string using the special syntax: ${ injectedVariable }. The convention used by JST is to use template string over string concatenation, 
                    but the developer is free to choose either. The following program demonstrates their equivalence.
                </ParagraphStyled>
            </DividerStyled>
    
            {/* string3.js */}
            <DividerStyled programCode>

                <Header fileName>
                    string3.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber3++}<IndentorStyled />let age = 11;<Breaker />
                    Line {lineNumber3++}<IndentorStyled />let stringConcatenation = "I am " + age + ".";<Breaker />
                    Line {lineNumber3++}<IndentorStyled />let templateString = `I am ${ age }.`;<Breaker />
                    Line {lineNumber3++}<IndentorStyled />console.log( `stringConcatenation === templateString:<Breaker />
                    <TripleIndentorStyled />${templateString}` );<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* string3.js */}

            {/* string3.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    stringConcatenation === templateString: true.<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* string3.js output */}

            <DividerStyled>

                {/* spacer from the last program output to make the reading easier */}
                <Breaker />

                <Header subsection>
                    2. Boolean
                </Header>

                <ParagraphStyled>
                    The Boolean data type can hold one of two possible values: true, or false. This data type may be used for 
                    status states which are binary in nature: for example, whether the user is logged in or not. In addition, 
                    the Boolean variables are indispensable in branching and loops, as the values they hold determine whether a 
                    specific branch needs to be taken, and whether the loop should terminate, respectively. The following 
                    program demonstrates variable declaration and definition in JS.
                </ParagraphStyled>

            </DividerStyled>

            {/* boolean.js */}
            <DividerStyled programCode>

                <Header fileName>
                    boolean.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber4++}<IndentorStyled />{comment} Declare a variable.<Breaker />
                    Line {lineNumber4++}<IndentorStyled />let isLoggedIn;<Breaker />
                    Line {lineNumber4++}<Breaker />
                    Line {lineNumber4++}<IndentorStyled />{comment} Without assignment, the variable has the value of undefined.<Breaker />
                    Line {lineNumber4++}<IndentorStyled />console.log( `isLoggedIn = ${ isLoggedIn }.` );<Breaker />
                    Line {lineNumber4++}<Breaker />
                    Line {lineNumber4++}<IndentorStyled />{comment} Defining its value.<Breaker />
                    Line {lineNumber4++}<IndentorStyled />isLoggedIn = true;<Breaker />
                    Line {lineNumber4++}<Breaker />
                    Line {lineNumber4++}<IndentorStyled />{comment} Verifying the value assigned<Breaker />
                    Line {lineNumber4++}<IndentorStyled />console.log( `isLoggedIn = ${ isLoggedIn }.` );
                </ParagraphStyled>

            </DividerStyled>
            {/* boolean.js */}

            {/* boolean.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    isLoggedIn = undefined.<Breaker />
                    isLoggedIn = true.
                </ParagraphStyled>

            </DividerStyled>
            {/* boolean.js output */}

            <DividerStyled>

                <ParagraphStyled>
                    Note that it is also possible to define a variable at the time it was declared. This has been left as an 
                    exercise for the developer to experiment.
                </ParagraphStyled>

            </DividerStyled>

            <DividerStyled>

                <Header subsection>
                    3. Number
                </Header>

                <ParagraphStyled>
                    Dissimilar to programming languages which distinguish the integer data type (whole numbers) from the 
                    floating-point data type (real numbers), the JS programming language has one encompassing data type, 
                    the Number type. The Number data type is suitable for information that involves any type of numerical 
                    data, such as age, the zip code, or account balance. All Number variables are stored in double-precision 
                    64-bit floating point format, and all mathematical operations are therefore floating-point. However, the 
                    calculated result may appear to be an integer. For example, while the result of 5 divided by 10 is 0.5 
                    (floating-point), the result of 10 divided by 5 is 2 (integer). Therefore, to render all calculation 
                    results in the floating-point format, the toFixed( ) function can be used: its argument specifies the 
                    number of significant digits after the decimal. The following program demonstrates its use.
                </ParagraphStyled>

            </DividerStyled>

            {/* number1.js */}
            <DividerStyled programCode>

                <Header fileName>
                    number1.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber5++}<IndentorStyled />{comment} All calculations are floating point.<Breaker />
                    Line {lineNumber5++}<IndentorStyled />console.log( `5/10 = ${number1a}.` );<Breaker />
                    Line {lineNumber5++}<IndentorStyled />{comment} But the result may still appear to be an integer.<Breaker />
                    Line {lineNumber5++}<IndentorStyled />console.log( `10/5 = ${number1b}.` );<Breaker />
                    Line {lineNumber5++}<IndentorStyled />{comment} Use toFixed() to remedy this issue.<Breaker />
                    Line {lineNumber5++}<IndentorStyled />console.log( `(10/5).toFixed(1) = ${number1c}.` );<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* number1.js */}

            {/* number1.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    5/10 = 0.5.<Breaker />
                    10/5 = 2.<Breaker />
                    (10/5).toFixed(1) = 2.0.
                </ParagraphStyled>

            </DividerStyled>
            {/* number1.js output */}

            <DividerStyled>

                    <ParagraphStyled>
                        However, the toFixed() function simply renders the result to appear as if they were integers: if integer division is desired, the 
                        appropriate function needs to be called from the Math library. If the result is expected to be positive, the Math.floor() function 
                        should be used; if the result is expected to be negative, the Math.ceil() function should be used. The names of these two library 
                        functions are indicative of the functionalities they provide: the Math.floor() function truncates the result, and the Math.ceil() 
                        function promotes the result. The following program demonstrates the proper use of these two functions.
                    </ParagraphStyled>
            
            </DividerStyled>

            {/* number2.js */}
            <DividerStyled programCode>

                <Header fileName>
                    number2.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber6++}<IndentorStyled />{comment} All calculations are floating point.<Breaker />
                    Line {lineNumber6++}<IndentorStyled />console.log( `6/5 = ${number2a}.` );<Breaker />
                    Line {lineNumber6++}<IndentorStyled />console.log( `-6/5 = ${number2b}.` );<Breaker />
                    Line {lineNumber6++}<Breaker />
                    Line {lineNumber6++}<IndentorStyled />{comment} If an integer result is necessary:<Breaker />
                    Line {lineNumber6++}<IndentorStyled />console.log( `Math.floor(6/5) = ${number2c}.` );<Breaker />
                    Line {lineNumber6++}<IndentorStyled />console.log( `Math.ceil(-6/5) = ${number2d}.` );<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* number2.js */}

            {/* number2.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    6/5 = 1.2.<Breaker />
                    -6/5 = -1.2.<Breaker />
                    Math.floor(6/5) = 1.<Breaker />
                    Math.ceil(-6/5) = -1.
                </ParagraphStyled>

            </DividerStyled>
            {/* number2.js output */}

            <DividerStyled>
                    <ParagraphStyled>
                        Using the inappropriate library function will yield the incorrect result when an integer result is 
                        desired, due to the manner in which these library functions have been implemented. This has been 
                        left as an exercise for the developer to verify.
                    </ParagraphStyled>
            </DividerStyled>

            <DividerStyled>

                <Header subsection>
                    4. Object
                </Header>

                <ParagraphStyled>
                    The Object data type is a type that encourages the grouping of related information, such as the user 
                    profile, which can include their first name, last name, social security number, and age. The 
                    individual fields of the object are known as properties in JS. In an upcoming lesson on functions, the 
                    Object data type will be explored further: in object-oriented programming (OOJ), an object can have 
                    functions that are associated with its properties, and these functions are called methods. In OOJ, the 
                    JS Object may be considered a data structure, since the variable encapsulates data and the methods that 
                    work on that data. The following program demonstrates how objects can be declared and modified. Note 
                    that not only can an objects property be accessed via the member operator (.), the same operator can 
                    also be used to add a property to that object. Objects in JS are defined inside curly braces ({object1a}). The 
                    follow program creates an object, and inserts a new property into it.
                </ParagraphStyled>

            </DividerStyled>

            {/* object.js */}
            <DividerStyled programCode>

                <Header fileName>
                    object.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber7++}<IndentorStyled />{comment} Define an object with a property.<Breaker />
                    Line {lineNumber7++}<IndentorStyled />let userProfile = {object1b};<Breaker />
                    Line {lineNumber7++}<Breaker />
                    Line {lineNumber7++}<IndentorStyled />{comment} Print the object.<Breaker />
                    Line {lineNumber7++}<IndentorStyled />console.log( userProfile );<Breaker />
                    Line {lineNumber7++}<IndentorStyled />{comment} Add a property to the object.<Breaker />
                    Line {lineNumber7++}<IndentorStyled />userProfile.gender = "female";<Breaker />
                    Line {lineNumber7++}<IndentorStyled />{comment} Print the object.<Breaker />
                    Line {lineNumber7++}<IndentorStyled />console.log( userProfile );<Breaker />
                </ParagraphStyled>

            </DividerStyled>
            {/* object.js */}

            {/* object.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    {object1b}<Breaker />
                    {object1c}
                </ParagraphStyled>

            </DividerStyled>
            {/* object.js output */}

            <DividerStyled>

                    <ParagraphStyled>
                        As aforementioned, the member operator (.) can be used to change existing properties of an object. 
                        This has been left as an exercise for the developer to complete.
                    </ParagraphStyled>
            
            </DividerStyled>

            <DividerStyled>

                <Header subsection>
                    5. Array
                </Header>

                <ParagraphStyled>
                    Lists, or arrays, are important because they have many applications: the list of the patients food 
                    allergies, the list of courses taken by the student, the list of accounts associated with the user, 
                    the list of items to purchase, and more. In programming languages such as C++ and Java, an array is a 
                    data structure that holds data of a homogeneous nature: this data structure can be utilized to keep 
                    track of the users hobbies, for example. In JS, however, array elements can be homogeneous or 
                    heterogenous. The following program verifies this fact.
                </ParagraphStyled>

            </DividerStyled>

            {/* array1.js */}
            <DividerStyled programCode>

                <Header fileName>
                    array1.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber8++}<IndentorStyled />let homogeneousArray = [ 1, 2 ];<Breaker />
                    Line {lineNumber8++}<IndentorStyled />let hetrogeneousArray = [ 3, 'JST' ];<Breaker />
                    Line {lineNumber8++}<Breaker />
                    Line {lineNumber8++}<IndentorStyled />{comment} Print the arrays.<Breaker />
                    Line {lineNumber8++}<IndentorStyled />console.log( homogeneousArray );<Breaker />
                    Line {lineNumber8++}<IndentorStyled />console.log( hetrogeneousArray );
                </ParagraphStyled>

            </DividerStyled>
            {/* array1.js */}

            {/* array1.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    [ 1, 2 ]<Breaker />
                    [ 3, 'JST' ]
                </ParagraphStyled>

            </DividerStyled>
            {/* array1.js output */}

            <DividerStyled>

                    <ParagraphStyled>
                        A stack is a common data structure, and JS provides array methods to work with stacks, namely 
                        the push() function to store value onto the top of the stack, and the pop() function to remove 
                        value from the top of the stack. Using these functions, a stack can be modelled using an array. 
                        The following program demonstrates the use of these functions.
                    </ParagraphStyled>
            
            </DividerStyled>

            {/* array2.js */}
            <DividerStyled programCode>

                <Header fileName>
                    array2.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber9++}<IndentorStyled />const array = [ 'J' ];<Breaker />
                    Line {lineNumber9++}<Breaker />
                    Line {lineNumber9++}<IndentorStyled />{comment} Push elements onto the array.<Breaker />
                    Line {lineNumber9++}<IndentorStyled />array.push( 'S' );<Breaker />
                    Line {lineNumber9++}<IndentorStyled />array.push( 'T' );<Breaker />
                    Line {lineNumber9++}<IndentorStyled />console.log( array );<Breaker />
                    Line {lineNumber9++}<IndentorStyled /><Breaker />
                    Line {lineNumber9++}<IndentorStyled />{comment} Pop the most recent element.<Breaker />
                    Line {lineNumber9++}<IndentorStyled />array.pop(); <Breaker />
                    Line {lineNumber9++}<IndentorStyled />console.log( array );
                </ParagraphStyled>

            </DividerStyled>
            {/* array2.js */}

            {/* array2.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    ['J', 'S', 'T']<Breaker />
                    [ 'J', 'S' ]
                </ParagraphStyled>

            </DividerStyled>
            {/* array2.js output */}
    
            <DividerStyled>
                    <ParagraphStyled>
                        In contrast to the push() function and the pop() function which work with the top of the stack (end of 
                        the array), JS also provides their counterparts for working with the beginning of the array, namely 
                        the unshift() function and the shift() function. For example, the shift() function can be used to 
                        dequeue an element from a to-do list.
                    </ParagraphStyled>
            </DividerStyled>

            {/* array3.js */}
            <DividerStyled programCode>

                <Header fileName>
                    array3.js
                </Header>

                <ParagraphStyled>
                    Line {lineNumber10++}<IndentorStyled />let todos = [ 'study', 'cook', 'sleep' ];<Breaker />
                    Line {lineNumber10++}<Breaker />
                    Line {lineNumber10++}<IndentorStyled />{comment} Finish one to-do from the top of the list.<Breaker />
                    Line {lineNumber10++}<IndentorStyled />todos.shift();<Breaker />
                    Line {lineNumber10++}<IndentorStyled />console.log( todos );<Breaker />
                    Line {lineNumber10++}<Breaker />
                    Line {lineNumber10++}<IndentorStyled />{comment} Add one new to-do to the top of the list.<Breaker />
                    Line {lineNumber10++}<IndentorStyled />todos.unshift( 'exercise');<Breaker />
                    Line {lineNumber10++}<IndentorStyled />console.log( todos );<Breaker />

                </ParagraphStyled>

            </DividerStyled>
            {/* array3.js */}

            {/* array3.js output */}
            <DividerStyled programOutput>

                <Header output>
                    output
                </Header>

                <ParagraphStyled>
                    [ 'cook', 'sleep' ]<Breaker />
                    [ 'exercise', 'cook', 'sleep' ]
                </ParagraphStyled>

            </DividerStyled>
            {/* array3.js output */}

            {/* lecture content */}

            <DividerStyled>
                {/* show review */}
                { !showReview &&
                    <AnchorStyled
                        recap
                        onClick={ toggleReview }>
                        Let's Recap
                    </AnchorStyled>
                }
                {/* show review */}

                {/* lesson review */}
                {
                    showReview && (
                        <>
                        <ParagraphStyled review>
                            <Header main>
                                Recap
                            </Header>
                            ({lineNumber11++}) The String type is appropriate for textual data.<Breaker />
                            ({lineNumber11++}) Template strings support real-time interpolation of variables.<Breaker />
                            ({lineNumber11++}) The Boolean type is appropriate for states which have only two possible values.<Breaker />
                            ({lineNumber11++}) The Number type supports both integers and floating-point numbers.<Breaker />
                            ({lineNumber11++}) The Object type allows new properties to be readily added through the member operator (.).<Breaker />
                            ({lineNumber11++}) The Array data structure can contain either homogeneous or heterogenous elements.
                        </ParagraphStyled>
                        </>
                    )
                }
                {/* lesson review */}

                {/* hide review */}
                {
                    showReview && (
                        <AnchorStyled 
                                recapDone
                                onClick={ toggleReview }>
                                I am done reviewing!
                        </AnchorStyled>
                    )
                }
                {/* hide review */}

            </DividerStyled>
            
            <DividerStyled pageLinker>
                <Footer>
                    <Link to='/hello-world'>&lArr; Back</Link>
                </Footer>

                <Footer>
                    <Link to='/expressions-and-operators'>Forward &rArr;</Link>
                </Footer>
            </DividerStyled>
        </>
    )
}
export default DataTypesAndStructures;